import React from "react";
import propTypes from 'prop-types';

export default class Dropdown extends React.PureComponent {
    constructor(props) {
        super(props)
        // this.state = { selVal: props.value };
    }

    onChange = (e) => {
        let selectedValue = e.currentTarget.value;
        // this.setState({ selVal: selectedValue });
        this.props.onChangeCB && this.props.onChangeCB(e, selectedValue);
    }

    render() {
        return (<select id="capital" value={this.props.value} onChange={this.onChange}>
            <option value="null">--Select--</option>
            {this.props.options.map(function (option, ind) {
                return <option key={ind.toString()} value={option[this.props.valMap]}>
                    {option[this.props.keyMap]}
                </option>
            }, this)}
        </select>)
    }
}
Dropdown.propTypes = {
    options: propTypes.array,
    keyMap: propTypes.string.isRequired,
    valMap: propTypes.string.isRequired,
    onChangeCB: propTypes.func,
    value: propTypes.any
}



